import { login, register } from '@/api/user'
// 仅在客户端加载这个包
const Cookie = process.client ? require('js-cookie') : undefined;
export default {
  middleware:'not-auth',
  name: 'LoginIndex',
  computed:{
    isLogin(){
      return this.$route.name === 'login'
    }
  },
  data(){
    return {
      user:{
        email:'',
        password:'',
        username: ''
      },
      errors: {}
    }
  },
  methods:{
    async onSubmit(){
      try{
        // 提交表单请求登录
        const { data } = this.isLogin 
        ? await login({
            user: this.user
          })
        : await register({
            user: this.user
          })
        // 保存用户的登录状态
        this.$store.commit('setUser', data.user)  
        // 为了防止刷新页面数据丢失
        Cookie.set('user', data.user)
        this.$router.push('/')
      }catch(err){
        this.errors = err.response.data.errors
      }
    }
  }
}
